$offset: 20px;
$time: 0.3s;

$color-brand: #0366d6;
$color-brand_lighter: #f1f8fe;
$color-brand_shady: #0057b9;
$color-sub-brand: #eb5757;
$color-extras: #f1e05a;
$color-text: #000;
$color-text_bright: #e0e0e0;
$color-text_light: #b3bcc7;
$color-text_lighter: #999999;

$color-bright: #fff;
$color-shadow: #3c4146;

.header {
  background-color: $color-shadow;
  display: flex;
  align-items: center;
  justify-content: space-between;
  // color: $color-bright;

  &__logo {
    margin-left: 30px;
  }

  &__control {
    padding-top: $offset;
    padding-right: $offset * 1.5;
    padding-bottom: $offset;
  }

  &__link {
    margin-right: $offset;
    font-size: 1em;
    color: $color-text_lighter;

    &:last-child {
      margin-right: 0;
    }

    &.active {
      color: $color-bright;
      cursor: default;
    }
  }
}
