$offset: 20px;
$time: 0.3s;

$color-brand: #0366d6;
$color-brand_lighter: #f1f8fe;
$color-brand_shady: #0057b9;
$color-sub-brand: #eb5757;
$color-extras: #f1e05a;
$color-text: #000;
$color-text_bright: #e0e0e0;
$color-text_light: #b3bcc7;
$color-text_lighter: #999999;

$color-bright: #fff;
$color-shadow: #3c4146;

.search {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  font-size: 14.5px;

  &__item {
    padding-right: 25px;
    flex: 1 1 auto;

    &:last-child {
      padding-right: 0;
    }
  }

  &__select,
  &__input {
    appearance: none;
    outline: none;
    width: 100%;
    height: 100%;
    background-color: inherit;
    border-radius: 0;
    border-top: none;
    border-right: none;
    border-bottom: 1px solid $color-brand;
    border-left: none;
    font-size: inherit;
    padding: 7px 0;
    min-height: 36px;

    &::placeholder {
      color: inherit;
    }
  }

  // &__label {
  //   position: absolute;
  //   z-index: -1;
  //   left: 0;
  //   top: 50%;
  //   transform: translateY(-50%);
  // }

  &__submit {
    &[disabled] {
      background-color: grey;
      cursor: default;
    }
  }
}
