$offset: 20px;
$time: 0.3s;

$color-brand: #0366d6;
$color-brand_lighter: #f1f8fe;
$color-brand_shady: #0057b9;
$color-sub-brand: #eb5757;
$color-extras: #f1e05a;
$color-text: #000;
$color-text_bright: #e0e0e0;
$color-text_light: #b3bcc7;
$color-text_lighter: #999999;

$color-bright: #fff;
$color-shadow: #3c4146;

.checkbox {
  width: 20px;
  height: 20px;
  margin-right: 19px;
  margin-top: 25px;
  border: solid 1px $color-brand;
  transition: background-color $time;

  &:hover,
  &:focus {
    background-color: $color-brand_lighter;
  }

  &_active {
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 10px;
      height: 10px;
      background-color: $color-brand;
    }
  }
}
