$offset: 20px;
$time: 0.3s;

$color-brand: #0366d6;
$color-brand_lighter: #f1f8fe;
$color-brand_shady: #0057b9;
$color-sub-brand: #eb5757;
$color-extras: #f1e05a;
$color-text: #000;
$color-text_bright: #e0e0e0;
$color-text_light: #b3bcc7;
$color-text_lighter: #999999;

$color-bright: #fff;
$color-shadow: #3c4146;

$defaultClassReactSelect: "[class*='css-']";

.select {
  padding: 0;
  &#{$defaultClassReactSelect} {
    appearance: none;
    outline: none;
  }

  &__control {
    &#{$defaultClassReactSelect} {
      background-color: inherit;
      border: none;
      min-height: inherit;
      appearance: none;
      outline: none;
      box-shadow: none;

      &:hover {
        appearance: none;
        outline: none;
        border: none;
        box-shadow: none;
      }
    }
  }

  &__value-container {
    &#{$defaultClassReactSelect} {
      padding: 0;
    }
  }

  &__placeholder,
  &__single-value {
    &#{$defaultClassReactSelect} {
      color: $color-text;
      margin: 0;
    }
  }

  &__dropdown-indicator {
    &#{$defaultClassReactSelect} {
      color: inherit;
    }
  }

  &__indicator-separator {
    &#{$defaultClassReactSelect} {
      display: none;
    }
  }

  &__input {
    > input {
      width: 100% !important;
    }
  }
}
