    border-radius: 15px;
    font-size: 12px;
    background-color: #f1f8fe; }
    .result-tile__tag:last-child {
      margin-right: 0; }


.search {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  font-size: 14.5px; }
  .search__item {
    padding-right: 25px;
    flex: 1 1 auto; }
    .search__item:last-child {
      padding-right: 0; }
  .search__select, .search__input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    outline: none;
    width: 100%;
    height: 100%;
    background-color: inherit;
    border-radius: 0;
    border-top: none;
    border-right: none;
    border-bottom: 1px solid #0366d6;
    border-left: none;
    font-size: inherit;
    padding: 7px 0;
    min-height: 36px; }
    .search__select::-webkit-input-placeholder, .search__input::-webkit-input-placeholder {
      color: inherit; }
    .search__select::-moz-placeholder, .search__input::-moz-placeholder {
      color: inherit; }
    .search__select:-ms-input-placeholder, .search__input:-ms-input-placeholder {
      color: inherit; }
    .search__select::-ms-input-placeholder, .search__input::-ms-input-placeholder {
      color: inherit; }
    .search__select::placeholder, .search__input::placeholder {
      color: inherit; }
  .search__submit[disabled] {
    background-color: grey;
    cursor: default; }

.select {
  padding: 0; }
  .select[class*='css-'] {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    outline: none; }
  .select__control[class*='css-'] {
    background-color: inherit;
    border: none;
    min-height: inherit;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    outline: none;
    box-shadow: none; }
    .select__control[class*='css-']:hover {
      -webkit-appearance: none;
         -moz-appearance: none;
              appearance: none;
      outline: none;
      border: none;
      box-shadow: none; }
  .select__value-container[class*='css-'] {
    padding: 0; }
  .select__placeholder[class*='css-'], .select__single-value[class*='css-'] {
    color: #000;
    margin: 0; }
  .select__dropdown-indicator[class*='css-'] {
    color: inherit; }
  .select__indicator-separator[class*='css-'] {
    display: none; }
  .select__input > input {
    width: 100% !important; }

.footer {
